.glitch::after,
.glitch::before {
  content: attr(data-text);
  top: 0;
  background-color: var(--color-primary);
  overflow: hidden;
  clip: rect(0, 900px, 0, 0)
}

.home-container {
  display: flex;
  flex-direction: row;
  background-color: var(--color-primary);
  max-width: 100vw;
}

.home-content {
  position: relative;
  overflow: hidden;
  color: var(--color-secondary);
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  flex-direction: column
}

.home-title {
  font-size: 220px;
  margin-top: 280px;
  user-select: none;
  font-family: Madena, sans-serif;
  font-weight: 600
}

.glitch {
  color: #fff;
  font-size: 60px;
  font-weight: bolder;
  position: relative;
  margin: 0 auto;
  background-color: var(--color-primary)
}

@keyframes noise-anim {

  0%,
  100% {
    clip: rect(28px, 9999px, 38px, 0)
  }

  5% {
    clip: rect(46px, 9999px, 15px, 0)
  }

  10% {
    clip: rect(64px, 9999px, 29px, 0)
  }

  15% {
    clip: rect(34px, 9999px, 30px, 0)
  }

  20% {
    clip: rect(10px, 9999px, 42px, 0)
  }

  25% {
    clip: rect(36px, 9999px, 56px, 0)
  }

  30% {
    clip: rect(44px, 9999px, 98px, 0)
  }

  35% {
    clip: rect(14px, 9999px, 36px, 0)
  }

  40% {
    clip: rect(50px, 9999px, 84px, 0)
  }

  45% {
    clip: rect(18px, 9999px, 62px, 0)
  }

  50% {
    clip: rect(90px, 9999px, 92px, 0)
  }

  55% {
    clip: rect(38px, 9999px, 42px, 0)
  }

  60% {
    clip: rect(84px, 9999px, 28px, 0)
  }

  65% {
    clip: rect(20px, 9999px, 20px, 0)
  }

  70% {
    clip: rect(64px, 9999px, 16px, 0)
  }

  75% {
    clip: rect(22px, 9999px, 14px, 0)
  }

  80% {
    clip: rect(34px, 9999px, 82px, 0)
  }
}

.glitch::after {
  position: absolute;
  left: 2px;
  text-shadow: -1px 0 red;
  color: #fff;
  animation: 2s linear infinite alternate-reverse noise-anim
}

@keyframes noise-anim-2 {
  0% {
    clip: rect(42px, 9999px, 18px, 0)
  }

  5% {
    clip: rect(64px, 9999px, 28px, 0)
  }

  10% {
    clip: rect(44px, 9999px, 72px, 0)
  }

  15% {
    clip: rect(30px, 9999px, 86px, 0)
  }

  20% {
    clip: rect(70px, 9999px, 46px, 0)
  }

  25% {
    clip: rect(50px, 9999px, 16px, 0)
  }

  30% {
    clip: rect(22px, 9999px, 26px, 0)
  }

  35% {
    clip: rect(56px, 9999px, 52px, 0)
  }

  40% {
    clip: rect(16px, 9999px, 86px, 0)
  }

  45% {
    clip: rect(90px, 9999px, 64px, 0)
  }

  50% {
    clip: rect(62px, 9999px, 44px, 0)
  }

  55% {
    clip: rect(82px, 9999px, 74px, 0)
  }

  60% {
    clip: rect(28px, 9999px, 98px, 0)
  }

  65% {
    clip: rect(42px, 9999px, 58px, 0)
  }

  70% {
    clip: rect(12px, 9999px, 16px, 0)
  }

  75% {
    clip: rect(68px, 9999px, 12px, 0)
  }
}

.glitch::before {
  position: absolute;
  left: -2px;
  text-shadow: 1px 0 #00f;
  color: #fff;
  animation: 3s linear infinite alternate-reverse noise-anim-2
}

.home-portfolio {
  font-size: 120px;
  font-family: Geologica;
  color: var(--color-secondary);
  font-weight: 600;
  margin-top: 20px;
  letter-spacing: 7px;
  transform: translateY(50px);
  opacity: 0;
  transition: transform .4s ease-in-out, opacity .5s ease-in-out;
  user-select: none
}

.home-name:hover {
  font-weight: 900
}

.polygon1 {
  height: 90px;
  width: 130px;
  background: linear-gradient(146deg, rgba(39, 35, 35, 0) 50%, #fff 50%);
  position: absolute;
  bottom: 180px;
  left: 210px;
  transform: perspective(1000px) rotate(150deg) rotateY(30deg) translateY(500px) translateX(500px);
  transition: transform .8s ease-in-out
}

.polygon1-active {
  transform: translateX(0) translateY(0) perspective(1000px) rotate(150deg) rotateY(30deg);
  animation: 4s ease-in-out 1s infinite animpoly1
}

@keyframes animpoly1 {

  0%,
  100% {
    transform: translateX(0) translateY(0) perspective(1000px) rotate(150deg) rotateY(30deg)
  }

  50% {
    transform: perspective(1000px) rotate(145deg) rotateY(35deg)
  }
}

.polygon2 {
  height: 50px;
  width: 75px;
  background: rgba(39, 35, 35, 0);
  background: linear-gradient(146deg, rgba(39, 35, 35, 0) 50%, var(--color-tertiary) 50%);
  position: absolute;
  bottom: 180px;
  left: 320px;
  transform: perspective(1000px) rotate(100deg) rotateY(30deg) translateX(400px);
  transition: transform .8s ease-in-out
}

.polygon3,
.polygon4 {
  background: rgba(39, 35, 35, 0);
  transition: transform .8s ease-in-out;
  position: absolute
}

.polygon2-active {
  transform: translateX(0) translateY(0) perspective(1000px) rotate(100deg) rotateY(30deg);
  animation: 3s ease-in-out 1.5s infinite animpoly2
}

@keyframes animpoly2 {

  0%,
  100% {
    transform: translateX(0) translateY(0) perspective(1000px) rotate(100deg) rotateY(30deg)
  }

  50% {
    transform: perspective(1000px) rotate(95deg) rotateY(35deg)
  }
}

.polygon3 {
  height: 150px;
  width: 150px;
  background: linear-gradient(135deg, rgba(39, 35, 35, 0) 50%, #fff 50%);
  bottom: 180px;
  right: 310px;
  transform: perspective(1000px) rotate(-80deg) rotateY(30deg) translateX(-400px) translateY(200px)
}

.polygon3-active {
  transform: translateX(0) translateY(0) perspective(1000px) rotate(-80deg) rotateY(30deg);
  animation: 4s ease-in-out 1.6s infinite animpoly3
}

@keyframes animpoly3 {

  0%,
  100% {
    transform: translateX(0) translateY(0) perspective(1000px) rotate(-80deg) rotateY(30deg)
  }

  50% {
    transform: perspective(1000px) rotate(-80deg) rotateY(15deg)
  }
}

.polygon4 {
  height: 110px;
  width: 200px;
  background: linear-gradient(28deg, rgba(39, 35, 35, 0) 50%, #fff 50%);
  top: 120px;
  left: 350px;
  transform: perspective(1000px) rotate(-20deg) rotateY(30deg) translateX(400px) translateY(-400px)
}

.polygon4-active {
  transform: translateX(0) translateY(0) perspective(1000px) rotate(-20deg) rotateY(30deg);
  animation: 3s ease-in-out 1.8s infinite animpoly4
}

@keyframes animpoly4 {

  0%,
  100% {
    transform: translateX(0) translateY(0) perspective(1000px) rotate(-20deg) rotateY(30deg)
  }

  50% {
    transform: perspective(1000px) rotate(-25deg) rotateY(35deg)
  }
}

.polygon5 {
  height: 85px;
  width: 130px;
  background: rgba(39, 35, 35, 0);
  background: linear-gradient(33deg, rgba(39, 35, 35, 0) 50%, #fff 50%);
  position: absolute;
  top: 150px;
  right: 400px;
  transform: perspective(1000px) rotate(-60deg) rotateY(30deg) translateX(400px) translateY(100px);
  transition: transform .8s ease-in-out
}

.polygon5-active {
  transform: translateX(0) translateY(0) perspective(1000px) rotate(-60deg) rotateY(30deg);
  animation: 4s ease-in-out 1.8s infinite animpoly5
}

.polygon1,
.polygon2,
.polygon3,
.polygon4,
.polygon5 {
  z-index: 1000
}

@keyframes animpoly5 {

  0%,
  100% {
    transform: translateX(0) translateY(0) perspective(1000px) rotate(-60deg) rotateY(30deg)
  }

  50% {
    transform: perspective(1000px) rotate(-55deg) rotateY(40deg)
  }
}

.icon-scroll:before {
  position: absolute;
  left: 50%;
  content: '';
  width: 8px;
  height: 8px;
  background: #fff;
  margin-left: -4px;
  top: 8px;
  border-radius: 4px;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-name: scroll
}

.icon-scroll {
  width: 40px;
  height: 70px;
  margin-left: -20px;
  margin-top: -35px;
  box-shadow: inset 0 0 0 1px #fff;
  border-radius: 25px;
  position: absolute;
  top: 50%;
  right: 2%;
  transform: translate(-50%, -50%)
}

.icon-scroll:after {
  content: 'Scroll';
  text-transform: uppercase;
  font-weight: 100;
  font-size: 13px;
  font-family: Grotesk;
  top: 105%;
  right: 50%;
  transform: translate(50%, 50%);
  position: absolute;
  color: #fff
}
.logo-portfolio-small{
  position: absolute;

  width: 40px;
  bottom: 40px;
  right: 40px;

}

@keyframes scroll {
  0% {
    opacity: 1
  }

  100% {
    opacity: 0;
    transform: translateY(46px)
  }
}

